import React, { Component } from 'react';
import { Col, Card, Row, Form, Popconfirm, message } from 'antd';
import { withRouter } from 'react-router-dom';
import Page from 'components/LayoutComponents/Page';
import {
  getPaperStudent,
  getPapeType,
  savePaperType,
  deletePaper,
} from 'services/AdditionalAdmissionPapersPage';
import { BtnDelete, BtnSave, BtnCancel } from 'components/common/button';
import {
  TableData,
  SelectAjax,
} from 'components/common';
import { dateToString } from '../../../utils/formatUtil';

@Form.create({ name: 'FormSearchStudent' })
class PaperStudentDetail extends Component {
  state = {
    reload: 0,
  };

  deletePaper = async record => {
    const {reload} = this.state;
    await deletePaper(this.props.match.params.id, record.paperTypeId);
    message.success('Xoá thành công');
    this.setState({
      reload:reload+1,
    });
  };

  savePaperType = async () => {
    const {reload} = this.state;
    await this.props.form.validateFields(async (err, values) => {
      await savePaperType(this.props.match.params.id, values.paperId);
      message.success('Thêm giấy tờ thành công');
      this.setState({
        reload:reload+1,
      });
    });
  };

  render() {
    const { getFieldDecorator, getFieldValue } = this.props.form;
    const { reload } = this.state;
    const paperId = getFieldValue('paperId');
    const columns = [
      {
        title: 'Mã giấy tờ',
        dataIndex: 'paperTypeId',
        key: 'paperTypeId',
      },
      {
        title: 'Tên giấy tờ',
        dataIndex: 'paperTypeLabel',
        key: 'paperTypeLabel',
      },
      {
        title: 'Ngày nhận',
        dataIndex: 'receivedDate',
        key: 'receivedDate',
        render: (text, record) => <div>{dateToString(record.receivedDate)}</div>,
      },
      {
        title: 'Thao tác',
        render: (text, record) => (
          <Popconfirm
            title="Đồng ý xóa ?"
            onConfirm={() => this.deletePaper(record)}
            okText="Xóa"
            cancelText="Hủy"
          >
            <BtnDelete />
          </Popconfirm>
        ),
      },
    ];
    return (
      <Card title="Thông tin giấy tờ">
        <Row>
          <Col span={24}>
            <Form layout="vertical" className="form-inline-label" onSubmit={this.handleSearch}>
              <Row gutter={24}>
                <Col span={18}>
                  <Form.Item label="Loại giấy tờ">
                    {getFieldDecorator('paperId', {})(
                      <SelectAjax
                        service={getPapeType}
                      />
                    )}
                  </Form.Item>
                </Col>
                <Col span={6}>
                  {paperId ? <BtnSave title='Nhận' onClick={this.savePaperType} /> : null}
                  <BtnCancel onClick={() => this.props.history.goBack()} />
                </Col>
              </Row>
            </Form>
          </Col>
          <Col span={24}>
            <TableData
              reload={reload}
              service={getPaperStudent}
              paramSearch={{studentId: this.props.match.params.id}}
              columns={columns}
              rowKey="paperTypeId"
            />
          </Col>
        </Row>
      </Card>
    );
  }
}

export default withRouter(props => (
  <Page {...props}>
    <PaperStudentDetail {...props} />
  </Page>
));



