import { request } from 'utils';

const prefix = '/api/students';

export const getStudents = dataSearch =>
  request({
    prefix,
    url: `/Student/Search`,
    params: { ...dataSearch },
    method: 'GET',
  });

export const getPaperStudent = ({studentId}) =>
  request({
    prefix,
    url: `/Student/${studentId}/GetSubmittedPaper`,
    // params: { studentId },
    method: 'GET',
  });

export const getPapeType = () =>
  request({
    prefix: '/api/categories',
    url: `/PaperType`,
    // params: { studentId },
    method: 'GET',
  });

export const savePaperType = (studentId, paperTypeId) =>
  request({
    prefix,
    url: `/Student/${studentId}/SubmitPaper`,
    data: { paperTypeId },
    method: 'POST',
  });
export const deletePaper = (studentId, paperTypeId) =>
  request({
    prefix,
    url: `/Student/${studentId}/SubmittedPaper/${paperTypeId}`,
    method: 'Delete',
  });

export const getUser = () =>
  request({
    prefix: '/identity',
    url: `/sign-in`,
    data: { username: 'admin', password: 'admin' },
    method: 'POST',
  });



